@import url(https://fonts.googleapis.com/css?family=Bigelow+Rules&display=swap);
*,*::after,*::before{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%}body{box-sizing:border-box;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:-webkit-gradient(linear, right top, left top, from(#071b52), to(teal));background:linear-gradient(to left, #071b52 0%, teal 100%)}.App{text-align:center}h1{font-family:'Bigelow Rules';font-size:7.2rem;color:#0ccac4}.card-list{width:85vw;margin:0 auto;display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:2rem}.card-container{display:flex;flex-direction:column;background-color:#95dada;border:1px solid grey;border-radius:.5rem;padding:2.5rem;cursor:pointer;-moz-osx-font-smoothing:grayscale;-webkit-backface-visibility:hidden;backface-visibility:hidden;-webkit-transform:translateZ(0);transform:translateZ(0);-webkit-transition:-webkit-transform .25s ease-out;transition:-webkit-transform .25s ease-out;transition:transform .25s ease-out;transition:transform .25s ease-out, -webkit-transform .25s ease-out}.card-container:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}.search{-webkit-appearance:none;border:none;outline:none;padding:1rem;width:15rem;line-height:3rem;margin-bottom:3rem}

